@font-face {
  font-family: 'proxima';
  src: url(../fonts/proximanova-regular.woff2) format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'proxima';
  src: url(../fonts/proximanova-semibold.woff2) format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'proxima';
  src: url(../fonts/proximanova-bold.woff2) format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'umnik';
  src: url(../fonts/umnik-bold.woff2) format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  font-family: sans-serif;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #f5f5f5;
  margin: 0;
  min-height: 100%;
  font-family: "proxima", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  color: #231f20;
  word-break: break-word; }

[role='button'] {
  cursor: pointer; }

input[type='search'] {
  appearance: textfield; }

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation;
  margin: 0; }

button,
input {
  overflow: visible;
  text-transform: none; }

button,
html [type='button'],
[type='reset'],
[type='submit'] {
  appearance: button; }

button:focus,
[type='button']:focus,
[type='reset']:focus,
[type='submit']:focus {
  padding: 0;
  border-style: none; }

input[type='date'],
input[type='time'],
input[type='datetime-local'],
input[type='month'] {
  appearance: listbox; }

textarea {
  overflow: auto;
  resize: vertical; }

svg {
  fill: currentColor; }
  a svg,
  button svg {
    pointer-events: none; }
  svg:not(:root) {
    overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  background-color: transparent;
  transition: color 0.3s;
  font-family: "proxima", sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  font-size: 18px;
  color: #232323; }
  a:hover, a:focus {
    outline: 0;
    text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  line-height: 1.2;
  color: currentColor; }

h1,
h2,
h3 {
  margin-top: 1em;
  margin-bottom: 0.5em; }

h4,
h5,
h6 {
  margin-top: 1em;
  margin-bottom: 0.5em; }

p,
ul,
ol,
dl,
table,
blockquote,
pre,
address,
figure {
  margin-top: 0;
  margin-bottom: 1em; }

address {
  font-style: normal; }

ul,
ol {
  padding-left: 1.5em; }

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0; }

li {
  line-height: inherit; }

dt {
  font-weight: 700; }

dd {
  margin-left: 0; }

hr {
  margin-top: 2rem;
  margin-bottom: 2rem;
  border: 0;
  border-top: 1px solid #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 80%; }

mark {
  background: #ff0;
  padding: 0.1em 0.3em; }

b,
strong {
  font-weight: bolder; }

abbr[title] {
  text-decoration: underline dotted; }

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0; }

code,
kbd,
pre,
samp {
  font-family: monospace; }

code {
  padding: 0.06em 0.3em;
  color: #231f20;
  background-color: #fff;
  border-radius: 0; }

kbd {
  padding: 0.06em 0.3em;
  color: #231f20;
  background-color: #fff;
  border-radius: 0; }
  kbd kbd {
    padding: 0; }

pre {
  display: block;
  width: 100%;
  overflow-x: auto;
  tab-size: 2; }

.btn {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  background-color: #ff0072;
  font-size: 18px;
  font-weight: bold;
  padding: 10px;
  box-shadow: 1px 1px 4px 0 black; }

.offer__title {
  padding: 0 15px; }

.offer__title h2 {
  font-size: 32px; }

.offer__wrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0 auto; }

.notebook-banner {
  background-image: url("../img/nb-banner-bg.jpg"); }
  @media (min-width: 768px) {
    .notebook-banner {
      max-width: 560px;
      margin: 0 auto; } }
  .notebook-banner__wrap {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 10px; }
    @media (min-width: 768px) {
      .notebook-banner__wrap {
        flex-direction: row;
        padding: 15px;
        margin: 0 -20px; } }
  .notebook-banner__img {
    padding: 0 10px; }
    @media (min-width: 768px) {
      .notebook-banner__img {
        max-width: 40%;
        padding: 0 20px; } }
  .notebook-banner__inner {
    padding: 0 10px; }
    @media (min-width: 768px) {
      .notebook-banner__inner {
        max-width: 60%;
        padding: 0 20px; } }
  .notebook-banner__img {
    position: relative;
    width: fit-content;
    margin: 0 auto; }
  .notebook-banner__img-link {
    display: block;
    cursor: pointer; }
  .notebook-banner__img-bonus {
    position: absolute;
    pointer-events: none;
    top: -10px;
    right: -20px; }
  .notebook-banner__title h3 {
    margin: 0;
    font-size: 26px;
    font-weight: bold;
    line-height: 1.3; }
    @media (min-width: 768px) {
      .notebook-banner__title h3 {
        font-size: 30px; } }
  .notebook-banner__descr {
    font-size: 15px; }
    @media (min-width: 768px) {
      .notebook-banner__descr {
        font-size: 16px; } }
  .notebook-banner__info {
    display: flex;
    justify-content: space-between;
    margin-top: 15px; }
  .notebook-banner__info-price-new {
    font-size: 36px;
    font-weight: bold;
    line-height: 1em;
    color: #ff0072; }
  .notebook-banner__info-price-old {
    position: relative;
    width: fit-content;
    font-size: 18px;
    font-weight: bold;
    line-height: 1em; }
  .notebook-banner__info-price-old svg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  .notebook-banner__info-link {
    display: inline-flex;
    margin-bottom: auto; }

.timer {
  display: flex;
  margin: 0 -10px; }
  .timer__hours, .timer__minutes, .timer__seconds {
    text-align: center;
    margin: 0 10px; }
  .timer__hours-count, .timer__minutes-count, .timer__seconds-count {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 55px;
    border-radius: 3px;
    background-color: #fff;
    font-family: Umnik, sans-serif;
    font-size: 55px;
    font-weight: 700; }
  .timer__hours-count:before, .timer__minutes-count:before, .timer__seconds-count:before {
    content: '';
    display: block;
    position: absolute;
    background-image: url("../img/count-seporator.png");
    width: 2px;
    height: 55px;
    z-index: 1; }

.game-banner {
  max-width: 320px;
  margin: 0 auto;
  margin-top: 30px; }
  @media (min-width: 768px) {
    .game-banner {
      max-width: 560px; } }
  .game-banner__link {
    display: block;
    position: relative;
    cursor: pointer; }
  .game-banner__link span {
    position: absolute;
    right: 18px;
    bottom: 18px;
    font-size: 8px;
    max-width: 70px; }
    @media (min-width: 768px) {
      .game-banner__link span {
        right: 33px;
        bottom: 27px;
        max-width: 120px;
        font-size: 16px;
        font-weight: normal; } }
